import React from 'react'
import Grid from '@material-ui/core/Grid'
import Avatar from '@material-ui/core/Avatar'
import { useHistory } from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton'
import NotificationsIcon from '@material-ui/icons/Notifications'
import ArrowDropDown from '@material-ui/icons/ArrowDropDown'

// Styles
import styles from './HeaderComponent.module.scss'
import images from 'Constant/images/images'

// Xurpas
// import { PopperPlacementType } from '@material-ui/core'
import { Components as XurpasComponents } from '@hims_client/hims-core'

export default function HeaderComponent ({ logout }) {
  const [anchorEl, setAnchorEl] = React.useState(null)

  const [openUmMenu, setOpenUmMenu] = React.useState(false)
  const [placementUmMenu, setPlacementUmMenu] = React.useState()

  const [openUmSwitcher, setOpenUmSwitcher] = React.useState(false)

  const wrapperRefXurpas = React.useRef(null)
  const history = useHistory()

  const handleShowMenu = (event) => {
    const newPlacement = 'bottom-ned'
    setOpenUmMenu(prev => placementUmMenu !== newPlacement || !prev)
    setPlacementUmMenu(newPlacement)
  }

  const redirectToProfile = () => {
    setOpenUmMenu(false)
    setAnchorEl(null)
    history.push('/profile')
  }

  return (
    <Grid item container className={styles.root}>
      <Grid item className={styles['left-container']}>
        <img src={images.logo} alt='logo' />
      </Grid>
      <Grid item className={styles['right-container']}>
        <IconButton>
          <NotificationsIcon />
        </IconButton>

        <Avatar alt='avatar' src={images.avatar} className={styles.avatar} />
        <IconButton
          aria-controls='simple-menu'
          aria-haspopup='true'
          onClick={event => { setAnchorEl(event.currentTarget); handleShowMenu(); event.preventDefault() }}
        >
          <ArrowDropDown />
        </IconButton>

        {/* <Menu
          id='header-menu'
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          keepMounted
          onClose={() => setAnchorEl(null)}
        >
          <MenuItem id='logout' onClick={handleClick('bottom-end')}>
            Logout
          </MenuItem>
        </Menu> */}

        <div >
          <XurpasComponents.UI.HeaderPopper
            open={openUmMenu}
            modules={[]}
            anchorEl={anchorEl}
            placement={placementUmMenu}
            wrapperRef={wrapperRefXurpas}
            modules={['Claims', 'Partner_Network']}
            currentModule='Claims'
            onClose={() => setOpenUmMenu(false)}
            onViewProfileClick={redirectToProfile}
            onLogout={() => logout()}
          />

        </div>
      </Grid>
    </Grid>
  )
}
